.footer__creators {
    opacity: 0;
    position: absolute;
    left: -100%;
}

@media only screen and (min-width: 1280px) {
    .footer__creators {
        max-width: 329px;
        left: 60%;
        top: -70%;
        padding: 20px 16px;
        background: #ff9900;
        border-radius: 14px 14px 14px 0px;
        width: 100%;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        -webkit-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
    }
}



