.landing__image {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    max-width: 366px;
    height: auto;
}

@media only screen and (min-width: 1024px) {
    .landing__image {
        max-width: 366px;
        position: absolute;
        right: 0;
        bottom: 0;
        height: auto;
    }
}

@media only screen and (min-width: 1230px) {
    .landing__image {
        max-width: 542px;
        position: static;
        -ms-flex-item-align: center;
            align-self: center;
    }
}


