.content__wrapper {
    margin: 0 auto;
    text-align: center;
    max-width: 944px;
    padding: 0 16px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

@media only screen and (min-width: 1024px) {
    .content__wrapper {
        padding: 0;
    }
}
@media only screen and (min-width: 1280px) {
    .content__wrapper {
        max-width: 1280px;
    }
}



